:root {
  --dark-bg: rgb(33, 37, 41);
}

.timeline-container {
  display: flex;
  flex-direction: column;
  position: relative;
  margin: 50px 0;
}

.timeline-container::after {
  background-color: #e17b77;
  content: "";
  position: absolute;
  left: calc(50% - 2px);
  width: 4px;
  height: 100%;
}

.dark-container::after {
  background-color: var(--dark-bg);
}

.timeline-item,
.single-timeline-item {
  display: flex;
  justify-content: flex-end;
  padding-right: 30px;
  position: relative;
  margin: 20px 0;
  width: 50%;
}

.single-timeline-item {
  width: 99%;
  margin-top: 70px;
}

.single-timeline-item .timeline-item-content {
  min-width: 100%;
}

.timeline-item:nth-child(odd) {
  align-self: flex-end;
  justify-content: flex-start;
  padding-left: 30px;
  padding-right: 0;
}

.timeline-item-content {
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.3);
  border-radius: 5px;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  padding: 15px;
  position: relative;
  width: 400px;
  max-width: 70%;
  text-align: right;
}

.timeline-item-content::after {
  content: " ";
  position: absolute;
  box-shadow: 1px -1px 1px rgba(0, 0, 0, 0.2);
  right: -7.5px;
  top: calc(50% - 7.5px);
  transform: rotate(45deg);
  width: 15px;
  height: 15px;
}

.bg-light::after {
  background-color: rgb(248, 249, 250);
}

.bg-dark::after {
  background-color: var(--dark-bg);
}

.timeline-item:nth-child(odd) .timeline-item-content {
  text-align: left;
  align-items: flex-start;
}

.timeline-item:nth-child(odd) .timeline-item-content::after {
  right: auto;
  left: -7.5px;
  box-shadow: -1px 1px 1px rgba(0, 0, 0, 0.2);
}

.timeline-item-content .tag {
  color: #fff;
  font-size: 12px;
  font-weight: bold;
  top: 5px;
  left: 5px;
  letter-spacing: 1px;
  padding: 5px;
  position: absolute;
  text-transform: uppercase;
}

.timeline-item:nth-child(odd) .timeline-item-content .tag {
  left: auto;
  right: 5px;
}

.timeline-item-content time {
  color: #777;
  font-size: 12px;
  font-weight: bold;
  margin-top: 30px;
}

.timeline-item-content p {
  font-size: 16px;
  line-height: 24px;
  margin: 15px 0;
}

.read-more,
.read-less {
  font-size: 14px;
  font-weight: bold;
  color: #3383b9;
  cursor: pointer;
}

.read-more::after {
  content: " ►";
  font-size: 12px;
}

.read-less::before {
  content: " ◀";
  font-size: 12px;
}

h5 {
  color: #3c778a;
}

.timeline-item-content .text2 {
  color: #777;
}

.project-list {
  list-style-type: none;
}

.timeline-item-content .circle {
  background-color: #fff;
  border-radius: 50%;
  position: absolute;
  top: calc(50% - 10px);
  right: -40px;
  width: 20px;
  height: 20px;
  z-index: 100;
}

.bg-light .circle {
  border: 3px solid #e17b77;
}

.bg-dark .circle {
  border: 3px solid var(--dark-bg);
}

.timeline-item:nth-child(odd) .timeline-item-content .circle {
  right: auto;
  left: -40px;
}

@media only screen and (max-width: 1023px) {
  .timeline-item-content {
    max-width: 100%;
  }
}

@media only screen and (max-width: 767px) {
  .timeline-item-content,
  .timeline-item:nth-child(odd) .timeline-item-content {
    padding: 15px 10px;
    text-align: center;
    align-items: center;
  }

  .timeline-item-content .tag {
    width: calc(100% - 10px);
    text-align: center;
    position: relative;
  }

  /* .timeline-item-content time {
    margin-top: 20px;
  } */

  .read-more,
  .read-less,
  .text2 {
    display: none;
  }
}
